import React, { useEffect, useContext, useState } from 'react'
import { FaPlayCircle } from 'react-icons/fa';

import Box from 'components/Box';
import Flex from 'components/Flex';
import Text from 'components/Text';
import Image from 'components/Image';
import Button from 'components/Button';
import BackgroundImage from 'components/BackgroundImage';
import FullpageVerticalCenter from 'components/FullpageVerticalCenter'
import Link from 'components/Link';

import AnswerContext from '../../AnswerContext'

import resbubble from './resbubble.svg';
import tutorialman from './tutorialman.svg';
import tutorial from './tutorial.svg';
import Layout from '../Layout'
import request from '../../utils/request'

const steps = ['溝通階段', '溝通解剖學模型', '溝通案例']

const datas = [
  {
    title: '設\n定',
    way:['定位', '目標', '策略'],
    step: '前',
    buttontext: '設定',
    links: ['https://url.relab.cc/health_1_1', 'https://url.relab.cc/health_1_2' ]
  },
  {
    title: '互\n動',
    way:['內容', '介面', '形式'],
    step: '中',
    buttontext: '互動',
    links: ['https://url.relab.cc/health_2_1', 'https://url.relab.cc/health_2_2']
  },
  {
    title: '驗\n證',
    way:['認識', '認知', '認同'],
    step: '後',
    buttontext: '驗證',
    links: ['https://url.relab.cc/health_3_1', 'https://url.relab.cc/health_3_2']
  },
]

const Tutorial = () => {
  const { answers, clearAnswer } = useContext(AnswerContext)
  const [answerClone, cloneAnswer] = useState([])
  useEffect(() => {
    if (answers && answers.length) {
      cloneAnswer(answers)
      request('https://us-central1-re-drive-75970.cloudfunctions.net/app/health-survey', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(answers)
      }).then(clearAnswer)
    }
  })
  const points = answerClone.reduce((res, a, i) => {
    const s = Math.floor(i / 3)
    res[s] = (res[s] || 0) + +a
    return res
  }, [])

  return (
    <Layout bg="blue" py="2em">
      <Box mx="25%" fontSize="1.25em" color="white">
        測驗的目的是幫助你找到自己容易忽略的內容，進一步強化衛教溝通。接下來，我們為你準備了一份衛教溝通設計指南，讓你能系統性地瞭解並練習衛教溝通的設計。
      </Box>
      <Flex pt="1.5em" px="4.5em">
        <Box width={1 / 2} py="1em" px="2.5em" mx="1em" bg="white" borderRadius="1em">
          <Text fontSize="1.5em">完整了解如何全面強化溝通，點擊下方前往衛教溝通設計指南</Text>
          <Box mt="1em" px="3em">
            <BackgroundImage src={tutorial} ratio={204.76 / 345.7} />
          </Box>
          <Box mt="1em" textAlign="center">
            <Button
              is={Link}
              rightIcon={FaPlayCircle}
              px="1.5em"
              borderRadius="1.5em"
              href="https://url.relab.cc/health_all"
            >前往衛教溝通設計指南</Button>
          </Box>
          <Box mt="1em">
            <Text fontSize="1.125em">完整計畫內容線上版，請分別點擊以下連結</Text>
          </Box>
          <Box color="black">
            <Link href="https://url.relab.cc/health_test">衛教溝通健檢測驗 https://url.relab.cc/health_test</Link>
          </Box>
          <Box color="black">
            <Link href="https://url.relab.cc/health_book">衛教溝通設計指南 https://url.relab.cc/health_book</Link>
          </Box>
        </Box>
        <Box width={1 / 2} py="1em" px="2.5em" mx="1em" bg="white" borderRadius="1em">
          <Text fontSize="1.25em">
            前面的九個問題，其實直接對應溝通解剖學模型的九個環節。因此你也可以從作答結果中自己較弱的環節，點選右方按鈕前往了解對應的方法及案例
          </Text>
          <Flex alignItems="center" justifyContent="space-around" pt="1em">
            {steps.map((step, index) => (
              <Box color="blue" position="relative" fontWeight="bold">
                <Text>{step}</Text>
                {index !== steps.length - 1 && (
                  <Box
                    position="absolute"
                    height="1px"
                    bg="blue"
                    width="2.25em"
                    right="0"
                    top="50%"
                    transform="translate(120%, -50%)"
                  >
                    <Box
                      position="absolute"
                      left="96%"
                      top="50%"
                      transform="translateY(-52%)"
                    >●</Box>
                  </Box>
                )}
              </Box>
            ))}
          </Flex>
          <Box px="1.75em">
            {datas.map(({ title, way, step, buttontext, links }, index) => (
              <Flex
                key={index}
                alignItems="center"
                py="0.5em"
                justifyContent="space-between"
                borderTop={index && '1px solid'}
              >
                <Box fontSize="1.625em">
                  溝通{step}
                </Box>
                <Flex alignItems="center">
                  <Box textAlign="center">
                    <Box
                      py="1.75rem"
                      px="0.5rem"
                      bg="blue"
                      color="white"
                      whiteSpace="pre-wrap"
                      borderRadius="1em"
                      fontSize="1.125em"
                      fontWeight="bold"
                      lineHeight="1.25"
                      position="relative"
                    >
                      {title}
                    </Box>
                  </Box>
                  <Box textAlign="center" mx="1em">
                    {way.map((w, i) => (
                      <Box key={i}>
                        <Box fontSize="0.875em" fontWeight="bold" color="blue">
                          {w}
                        </Box>
                        {i !== way.length - 1 && <Box lineHeight="1.25">|</Box>}
                      </Box>
                    ))}
                  </Box>
                </Flex>
                <Box textAlign="center">
                  {links.map((link, k) => (
                    <Box my="0.5em">
                      <Button
                        rightIcon={FaPlayCircle}
                        px="0.75em"
                        py="0.5em"
                        borderRadius="1.5em"
                        is={Link}
                        href={link}
                      >
                      {buttontext}{k ? '相關案例' : '方法說明'}
                    </Button>
                  </Box>
                  ))}
                </Box>
              </Flex>
            ))}
          </Box>
        </Box>
      </Flex>
    </Layout>
  )
}

export default Tutorial
